import React, { useEffect, useState } from "react";
import Home from "./pages/Home";
import Movies from "./pages/Movies";
import Detail from "./pages/Detail";
import { Routes, Route } from "react-router-dom";
import "./App.css";
import Header from "./components/header/Header";
import NotPath from "./components/error/NotPath";

function App() {
  const [scrollY, setScrollY] = useState(0);
  const onScrollY = () => {
    setScrollY(window.scrollY);
  };
  useEffect(() => {
    window.addEventListener("scroll", onScrollY);
    // return window.removeEventListener('scroll',onScrollY)
  });

  // 3  , , , 
  //     .
  // 3      . (, , )
  //     , , , ,    
  //        
  //        
  //    ,    
  //    
  //   ,  ( )

  return (
    <div className="App">
      {/*   0       */}
      {scrollY > 0 ? null : <Header />}

      <Routes>
        <Route path="*" element={<NotPath/>}/> 
        <Route path="/" element={<Home />}></Route>
        <Route path="/movies" element={<Movies />}></Route>
        <Route path="/search" element={<Movies />}></Route>
        <Route path="/detail/:id" element={<Detail />}></Route>
      </Routes>
    </div>
  );
}

export default App;
