import React from "react";
import styles from "./NotPath.module.css";
import { useNavigate } from "react-router-dom";


const NotPath = () => {


    const navigate = useNavigate()
  return (
    <article className={styles.not_path}>
      <div className={styles.not_path_inner_con}>
        <h1>The path does not exist. Would you like to go back to the previous page?</h1>
        <h3 style={{padding:"15px", color:"GrayText"}}>     .   ?</h3>
        <button
            className={styles.not_path_backButton} 
            onClick={()=>{
            navigate(-1)
        }}> Go back </button>
      </div>
    </article>
  );
};

export default NotPath;
